import {AppState} from '../../store/app.reducer';
import {TreksModel} from '../../roles/business/treks/treks.model';
import {DemandActions, SET_PRICE} from './demand.actions';
import {TrekPersonDetailsModel} from '../treks/trekPersonDetails.model';
import {EventsModel} from '../../roles/business/events/events.model';
import {EventPersonDetailsModel} from '../events/eventPersonDetails.model';

export interface DemandState extends AppState {
  demandState: State;
}

export interface State {
  eventDemand: {
    allEvents: any,
    activeEvent: EventsModel,
    selectedDetails: EventPersonDetailsModel
  };
  trekDemand: {
    allTreks: any,
    activeTrek: TreksModel,
    selectedDetails: TrekPersonDetailsModel
  };
  finalPrice: number;
}

const initialState: State = {
  eventDemand: {
    activeEvent: null,
    allEvents: null,
    selectedDetails: null
  },
  trekDemand: {
    allTreks: null,
    activeTrek: null,
    selectedDetails: null
  },
  finalPrice: 0
};

export function DemandReducer(state = initialState, action: DemandActions) {
  switch (action.type) {

    case 'SET_SELECTED_EVENT_DETAILS': {
      return {
        ...state,
        eventDemand: {
          ...state.eventDemand,
          selectedDetails: action.payload
        }
      };
    }

    case 'SET_SELECTED_TREK_DETAILS': {
      return {
        ...state,
        trekDemand: {
          ...state.trekDemand,
          selectedDetails: action.payload
        }
      };
    }
    case 'SET_PRICE': {
      return {
        ...state,
        finalPrice: action.payload
      };
    }
    case 'SET_ALL_TREK': {
      return {
        ...state,
        trekDemand: {
          ...state.trekDemand,
          allTreks: action.payload
        }
      };
    }
    case 'SET_ACTIVE_TREK': {
      return {
        ...state,
        trekDemand: {
          ...state.trekDemand,
          activeTrek: action.payload
        }
      };
    }
    case 'SET_ALL_EVENT': {
      return {
        ...state,
        eventDemand: {
          ...state.eventDemand,
          allEvents: action.payload
        }
      };
    }
    case 'SET_ACTIVE_EVENT': {
      return {
        ...state,
        eventDemand: {
          ...state.eventDemand,
          activeEvent: action.payload
        }
      };
    }

    default: {
      return {
        ...state
      };
    }
  }
}
