import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {DemandRoutingModule} from './demand-routing.module';
import {DemandComponent} from './demand.component';
import {StoreModule} from '@ngrx/store';
import {EffectsModule} from '@ngrx/effects';
import {DemandReducer} from './store/demand.reducer';
import {DemandEffects} from './store/demand.effects';


@NgModule({
  declarations: [
    DemandComponent,
  ],
  imports: [
    CommonModule,
    DemandRoutingModule,
    StoreModule.forFeature('demand', DemandReducer),
    EffectsModule.forFeature([DemandEffects])
  ]
})
export class DemandModule {
}
