import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as DemandActions from './demand.actions';
import * as endPoints from '../../shared/serverEndpoints';
import {map, switchMap} from 'rxjs/operators';

Injectable();

export class DemandEffects {
  constructor(public action: Actions, public http: HttpClient) {
  }

  @Effect()
  fetchSingleEvent = this.action.pipe(
    ofType(DemandActions.TRY_FETCH_EVENT),
    map((data: DemandActions.TryFetchEvent) => {
      return data.payload;
    }),
    switchMap((data: {
      _id: string
    }) => {
      return this.http.post(endPoints.eventDetails, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: DemandActions.SET_ACTIVE_EVENT,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );

  @Effect()
  fetchSingleTrek = this.action.pipe(
    ofType(DemandActions.TRY_FETCH_TREK),
    map((data: DemandActions.TryFetchTrek) => {
      return data.payload;
    }),
    switchMap((data: {
      _id: string
    }) => {
      return this.http.post(endPoints.trekDetails, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: DemandActions.SET_ACTIVE_TREK,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );
}
