import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';

const routes: Routes = [
  {
    path: 'treks', loadChildren: () => import('./treks/treks.module').then(module => module.TreksModule),
  },
  {
    path: 'events', loadChildren: () => import('./events/events.module').then(module => module.EventsModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DemandRoutingModule {
}
